<template>
    <div>
        <adt-search-folder
            :searchRouter="{query: {sceneCode: search.sceneCode, sceneName: search.sceneName, serverType: search.serverType, trspType: search.trspType, examineStatus: search.examineStatus, useStatus: search.useStatus}}"
            @on-triggerFolder="searchChange" :isStatus="true">
            <template slot="status">
                <div class="status">
                    <Col span="10">
                        <div class="name">审核状态:</div>
                        <span v-for="status in dictionaryApprovalStatus" :key="status.dictionaryKey" @click="auditStatus(status.dictionaryKey)" :class="{'click-status': search.examineStatus === status.dictionaryKey}">
                            {{ status.dictionaryValue }}
                        </span>
                    </Col>
                    <Col span="14">
                        <div class="name">启用状态:</div>
                        <span v-for="status in dictionaryEnabledStatus" :key="status.dictionaryKey" @click="enableStatus(status.dictionaryKey)" :class="{'click-status': search.useStatus === status.dictionaryKey}">
                            {{ status.dictionaryValue }}
                        </span>
                    </Col>
                </div>
            </template>
            <template slot="always-show">
                <Col span="4" class="search-condition">
                    <span> 场景编号：</span>
                    <Input v-model="search.sceneCode" clearable></Input>
                </Col>
                <Col span="4" class="search-condition line-margin">
                    <span> 场景名称：</span>
                    <Input v-model="search.sceneName" clearable></Input>
                </Col>
                <Col span="4" class="search-condition line-margin">
                    <span> 服务类型：</span>
                    <Select v-model="search.serverType" class="fromItem-content" clearable>
                        <Option v-for="(selectData, id) in dictionaryServiceType" :key="id" :value="selectData.dictionaryKey">{{selectData.dictionaryValue}}</Option>
                    </Select>
                </Col>
                <Col span="4" class="search-condition line-margin">
                    <span> 运输类型：</span>
                    <Select v-model="search.trspType" class="fromItem-content" clearable>
                        <Option v-for="(selectData, id) in dictionaryTransportType" :key="id" :value="selectData.dictionaryKey">{{selectData.dictionaryValue}}</Option>
                    </Select>
                </Col>
            </template>
        </adt-search-folder>
        <div class="btns-paginator">
            <AdtButton>
                <Button @click="add()">新增场景<Icon type="ios-add-circle-outline" /></Button>
                 <Button @click="startUsing()">{{ '批量启用' + (isManager ? '' : '申请') }}<Icon type="ios-add-circle-outline" /></Button>
                    <Button @click="stopUsing()">
                        {{ '批量停用' + (isManager ? '' : '申请') }}
                        <svg class="iconfont" aria-hidden="true">
                            <use xlink:href="#icon-tingyong" />
                        </svg>
                    </Button>
            </AdtButton>
            <adt-paginator class="pagination" :currentPage="~~page" :total="~~total"/>
        </div>
        <AdtMyModal :title="modelTitle" okText="保存" cancelText="取消" :showModal="isModalShow" @on-visible-change="addvisibleChange" @on-ok="saveAdd()" @on-cancel="cancelAdd()" width="1200">
            <!-- 新增场景 -->
            <div v-if="distinguish == 1">
                <Form class="fromItem" :model="addFormItem" ref="addFormItemData" :rules="addFormItemRule" :label-width="100" label-colon>
                    <FormItem label="场景名称" prop="sceneName">
                        <Input class="fromItem-content" v-model="addFormItem.sceneName"></Input>
                    </FormItem>
                    <FormItem class="fromItem-margin" label="服务类型" prop="serverType">
                        <Select v-model="addFormItem.serverType" class="fromItem-content" @on-select="selectType">
                            <Option v-for="(selectData, id) in dictionaryServiceType" :key="id" :tag="serviceTag" :value="selectData.dictionaryKey">{{selectData.dictionaryValue}}</Option>
                        </Select>
                    </FormItem>
                    <FormItem class="fromItem-margin" label="运输类型" prop="trspType">
                        <Select v-model="addFormItem.trspType" class="fromItem-content" @on-select="selectType">
                            <Option v-for="(selectData, id) in dictionaryTransportType" :key="id" :tag="transportTag" :value="selectData.dictionaryKey">{{selectData.dictionaryValue}}</Option>
                        </Select>
                    </FormItem>
                    <FormItem class="fromItem-margin" label="物流类型" prop="logisticsType">
                        <Select v-model="addFormItem.logisticsType" class="fromItem-content" @on-select="selectType">
                            <Option v-for="(selectData, id) in dictionaryLogisticsType" :key="id" :tag="logisticsTag" :value="selectData.dictionaryKey">{{selectData.dictionaryValue}}</Option>
                        </Select>
                    </FormItem>
                </Form>
            </div>
            <!-- 编辑场景 -->
            <div v-if="distinguish == 2">
                <div class="scene-top">场景编号{{sceneCode}}</div>
                <div class="scene-edit">
                    <Form class="fromItem" :model="editFormItem" ref="editFormItemData" :rules="editFormItemRule" :label-width="75" label-colon>
                        <FormItem label="场景名称" prop="accountEmail">
                            <Input class="fromItem-content" v-model="editFormItem.sceneName"></Input>
                        </FormItem>
                        <FormItem class="fromItem-margin" label="服务类型" prop="serverType">
                            <Select v-model="editFormItem.serverType" class="fromItem-content" @on-select="selectType">
                                <Option v-for="(selectData, id) in dictionaryServiceType" :key="id" :tag="serviceTag" :value="selectData.dictionaryKey">{{selectData.dictionaryValue}}</Option>
                            </Select>
                        </FormItem>
                        <FormItem class="fromItem-margin" label="运输类型" prop="trspType">
                            <Select v-model="editFormItem.trspType" class="fromItem-content" @on-select="selectType">
                                <Option v-for="(selectData, id) in dictionaryTransportType" :key="id"  :tag="transportTag" :value="selectData.dictionaryKey">{{selectData.dictionaryValue}}</Option>
                            </Select>
                        </FormItem>
                        <FormItem class="fromItem-margin" label="物流类型" prop="logisticsType">
                            <Select v-model="editFormItem.logisticsType" class="fromItem-content" @on-select="selectType">
                                <Option v-for="(selectData, id) in dictionaryLogisticsType" :key="id" :tag="logisticsTag" :value="selectData.dictionaryKey">{{selectData.dictionaryValue}}</Option>
                            </Select>
                        </FormItem>
                    </Form>
                </div>
            </div>
            <hr class="line-solid">
            <adt-tab-btns :height="40" :isBaseBtn="true" :titleNames="titleNames" :btnIndex="btnIndex" @handleBtn="handleBtn" :width="255">
                <div class="line"></div>
                <div>
                    <!-- 选择基础任务 -->
                    <div class="tabs" v-show="btnIndex == 1">
                        <div class="tabs-task">
                            <div class="tabs-search">
                                <span>基础任务名称：</span>
                                <div class="tabs-condition">
                                    <Input class="tabs-condition-input" type="text" v-model="basicTaskName"></Input>
                                    <Icon class="tabs-condition-logo" type="md-search" @click="searchTask(1)" />
                                </div>
                                <Button class="tabs-btn" type="primary" @click="sort(1)">排序</Button>
                            </div>
                            <Table :columns="tableColumnsBasicTask" :data="tableDataBasicTask" @on-selection-change="checkBasicTask" stripe>
                                <template #sort="{ row, index }">
                                    <div style="position: relative;">
                                        <span class="required-tag" style="width: 30px">*</span><Input style="width: 80px" v-model.trim="row.sort" @on-change="changeRowData(row, index, 'sort', 'sortBasic')" clearable></Input>
                                    </div>
                                </template>
                            </Table>
                        </div>
                        <div class="tabs-line"></div>
                        <!-- 基础任务排序 -->
                        <div class="tabs-sort">
                            <div class="tabs-title">基础任务排序:</div>
                            <Table :columns="tableColumnsBasicTaskSort" :data="tableDataBasicTaskSort" stripe></Table>
                        </div>
                    </div>
                    <!-- 选择附加任务 -->
                    <div class="tabs" v-show="btnIndex == 2">
                        <div class="tabs-task">
                            <div class="tabs-search">
                                <span>附加任务名称：</span>
                                <div class="tabs-condition">
                                    <Input class="tabs-condition-input" type="text" v-model="affixTaskName"></Input>
                                    <Icon class="tabs-condition-logo" type="md-search" @click="searchTask(2)" />
                                </div>
                                <Button class="tabs-btn" type="primary" @click="sort(2)">排序</Button>
                            </div>
                            <Table :columns="tableColumnsAdditionalTasks" :data="tableDataAdditionalTasks" @on-selection-change="checkBasicTask" stripe>
                                <template #sort="{ row, index }">
                                    <div style="position: relative;">
                                        <span class="required-tag" style="width: 30px">*</span><Input style="width: 80px" v-model.trim="row.sort" @on-change="changeRowData(row, index, 'sort', 'sortAttach')" clearable></Input>
                                    </div>
                                </template>
                            </Table>
                        </div>
                        <div class="tabs-line"></div>
                        <!-- 附加任务排序 -->
                        <div class="tabs-sort">
                            <div class="tabs-title">基础任务排序:</div>
                            <Table :columns="tableColumnsAdditionalTasksSort" :data="tableDataAdditionalTasksSort" stripe></Table>
                        </div>
                    </div>
                    <!-- 选择单据 -->
                    <div class="tabs" v-show="btnIndex == 3">
                        <div class="tabs-task">
                            <div class="tabs-search">
                                <span>单据名称：</span>
                                <div class="tabs-condition">
                                    <Input class="tabs-condition-input" type="text" v-model="billName"></Input>
                                    <Icon class="tabs-condition-logo" type="md-search" @click="searchTask(3)" />
                                </div>
                                <Button class="tabs-btn" type="primary" @click="sort(3)">排序</Button>
                            </div>
                            <Table :columns="tableColumnsDocument" :data="tableDataDocument" @on-selection-change="checkBasicTask" stripe>
                                <template #sort="{ row, index }">
                                    <div style="position: relative;">
                                        <span class="required-tag" style="width: 30px">*</span><Input style="width: 80px" v-model.trim="row.sort" @on-change="changeRowData(row, index, 'sort', 'sortDocument')" clearable></Input>
                                    </div>
                                </template>
                            </Table>
                        </div>
                        <div class="tabs-line"></div>
                        <!-- 单据排序 -->
                        <div class="tabs-sort">
                            <div class="tabs-title">单据排序:</div>
                            <Table :columns="tableColumnsDocumentSort" :data="tableDataDocumentSort" stripe></Table>
                        </div>
                    </div>
                </div>
                <div class="public-transit scen-select">
                    <Form ref="formScenes" :model="formScenes" inline label-colon>
                        <FormItem label="必须共存场景" class="select-transit">
                            <!-- <Select
                                v-model="formScenes.scene"
                                filterable
                                remote
                                :remote-method="sceneRemote"
                                :loading="sceneLoading"
                                @on-select="onChangeClick">
                                <Option v-for="(option, index) in sceneData" :value="option.value" :key="index">{{option.label}}</Option>
                            </Select> -->
                            <Select v-model="formScenes.symbiosisScene" filterable :loading="sceneLoading"
                                @on-select="onChangeClick">
                                <Option  v-for="(option, index) in sceneData" :value="option.label" :key="index">{{option.value}}</Option>
                            </Select>
                            <p class="select-value">
                                <span v-for="(value, index) in sceneSelectList" :key="index">{{value.label}}<Icon type="ios-close-circle" @click="delTransit(index)"/></span>
                            </p>
                        </FormItem>
                        <FormItem label="无法共存场景" class="select-transit">
                            <!-- <Select
                                v-model="formScenes.sceneNo"
                                filterable
                                remote
                                :remote-method="sceneNoRemote"
                                :loading="sceneNoLoading"
                                @on-select="noChangeClick">
                                <Option v-for="(option, index) in sceneNoData" :value="option.value" :key="index">{{option.label}}</Option>
                            </Select> -->
                            <Select v-model="formScenes.exclusiveScene" filterable :loading="sceneNoLoading"
                                @on-select="noChangeClick">
                                <Option  v-for="(option, index) in sceneData" :value="option.label" :key="index">{{option.value}}</Option>
                            </Select>
                            <p class="select-value">
                                <span v-for="(value, index) in sceneNoSelectList" :key="index">{{value.label}}<Icon type="ios-close-circle" @click="delTransitNo(index)"/></span>
                            </p>
                        </FormItem>
                    </Form>
                </div>
            </adt-tab-btns>
        </AdtMyModal>
        <Table :columns="tableColumns" :data="tableData" @on-selection-change="selectionChange" stripe>
            <!-- 审核状态 -->
            <template slot-scope="{ row }" slot="auditStatus">
                <span :class="{success: row.examineStatus == 'pass', failure: row.examineStatus == 'fail'}">{{row.examineStatus}}</span>
            </template>
            <!-- 启用状态 -->
            <template slot-scope="{ row }" slot="enableStatus">
                <span :class="{success: row.useStatus == 'enable', failure: row.useStatus == 'disable'}">{{row.useStatus}}</span>
            </template>
            <!-- 操作 -->
            <!-- <template slot-scope="{ row }" slot="action">
                <span class="icon-action" @click="startUsing(row)" v-if="row.useStatus !== 'enable' && row.examineStatus !== 'wait'">启</span>
                <span class="icon-action" @click="stopUsing(row)" v-if="row.useStatus === 'enable' && row.examineStatus !== 'wait'">禁</span>
                <Icon class="icon-action" type="ios-contact-outline" @click="examine(row)" v-if="isManager && row.examineStatus === 'wait'" />
                <Icon class="icon-action" type="md-create" @click="edit(row)" />
                <Icon class="icon-action" type="md-close" @click="remove(row)" />
                <Icon class="icon-action" type="ios-alert-outline" @click="detail(row)" />
            </template> -->
            <template #action="{ row }">
                <!-- 审核状态为待审核（wait），不显示启用停用按钮 -->
                <span class="icon-action icon-action-word" @click="startUsing(row)" v-if="
                (!row.useStatus || row.useStatus.trim() === '已停用') && row.examineStatus !== '待审核'">启</span>
                <span class="icon-action icon-action-word" @click="stopUsing(row)"  v-if="(row.useStatus && row.useStatus.trim() === '已启用') && row.examineStatus !== '待审核'">停</span>
                <Icon class="icon-action" type="ios-contact-outline" @click="examine(row)" v-if="isManager && row.examineStatus === '待审核'" />
                <!-- <Icon class="icon-action" type="md-create" @click="edit(row)" /> -->
                <!-- <Icon class="icon-action" type="md-close" @click="remove(row)" /> -->
                <Icon class="icon-action" type="ios-alert-outline" @click="detail(row)" />
            </template>
        </Table>
        <!-- 场景审核 -->
        <adt-my-modal
            :title="examineModalTitle"
            :showModal="isAuditShow"
            @on-visible-change="auditvisibleChange"
            @on-ok="saveAudit()"
            width="600">
            <!-- <Row>
                <Col span="8">
                    场景编号：{{ examineInfo.sceneCode }}
                </Col>
                <Col span="8">
                    操作人：GD53453
                </Col>
                <Col span="8">
                    操作时间：GD53453
                </Col>
            </Row> -->
            <RadioGroup v-model="agree" style="margin-bottom: 20px;">
                <Radio label="1">同意{{ examineType === 1 ? '启用' : '停用' }}</Radio>
                <Radio label="0" style="margin-left: 30px;">不同意{{ examineType === 1 ? '启用' : '停用' }}</Radio>
            </RadioGroup>
            <div v-show="agree === '0'">
                <div class="reason-name" style="margin-bottom: 6px;">填写驳回原因:</div>
                <Input type="textarea" v-model.trim="reason" placeholder="请输入内容..." />
            </div>
        </adt-my-modal>
        <adt-reconfirm-modal :showModal="isActionDel" @on-visible-change="visibleChange" @on-ok="allDelClick()" />
    </div>
</template>

<script>
import {tableColumns, tableColumnsBasicTask, tableColumnsAdditionalTasks, tableColumnsAdditionalTasksSort, tableColumnsDocument, tableColumnsDocumentSort, tableColumnsBasicTaskSort} from './data'
import paginator from '@/mixins/paginator'
import validateRules from '@/libs/validateRules'
import { timestampFormat, isRepeat, dateToString } from '@/libs/utils'
import{ apiQueryPage, apiEnable, apiDisable, apiExamEnablePass, apiExamEnableFail, apiExamDisablePass, apiExamDisableFail, apiInitSceneAdd, apiQueryTaskBase, apiQueryTaskAffix, apiAdd } from '@/api/sceneManage'
export default {
    mixins: [paginator],
    data() {
        return {
            userType: '',
            modelTitle: '',
            isModalShow: false,
            isActionDel: false,
            isAuditShow: false,
            isEditAuditShow: false,
            isManager: false,
            examineType: 1,
            examineModalTitle: '启用审核',
            value: '1',
            // 新增编辑区分  1为新增 2为编辑
            distinguish: 0,
            // 区分当前是否可以调用接口
            judgment: 0,
            search: {
                examineStatus: '',
                useStatus: '',
                sceneCode: '',
                sceneName: '',
                serverType: '',
                trspType: ''
            },
            // 新增搜索名称
            basicTaskName: '',
            affixTaskName: '',
            billName: '',
            // 新增需要的类型
            addType: {},
            auditStatusNum: '默认值',
            enableStatusNum: '默认值',
            titleNames: ['基础任务映射设置', '附加任务映射设置', '单据映射设置'],
            btnIndex: 1,
            addFormItem: {},
            addFormItemRule: {
                sceneName: [validateRules.required('场景名称')[0]],
                serverType: [validateRules.select('服务类型')[0]],
                trspType: [validateRules.select('运输类型')[0]],
                logisticsType: [validateRules.select('物流类型')[0]]
            },
            sceneCode: '',
            editFormItem: {},
            editFormItemRule: {},
            // 服务类型
            dictionaryServiceType: this.$getDictionary('SERVICE_TYPE') || [],
            // 运输类型
            dictionaryTransportType: this.$getDictionary('TRANSPORT_TYPE') || [],
            // 物流类型
            dictionaryLogisticsType: this.$getDictionary('LOGISTICS_TYPE') || [],
            // 审核状态
            dictionaryApprovalStatus: this.$getDictionary('AUDIT_STATUS') || [],
            // 启用状态
            dictionaryEnabledStatus: this.$getDictionary('ENABLE_STATUS') || [],
            tableColumnsBasicTask,
            tableColumnsAdditionalTasks,
            tableColumnsDocument,
            tableDataBasicTaskAudit: [],
            tableDataAdditionalTasksAudit: [],
            tableDataDocumentAudit: [],
            // 基础任务
            tableDataBasicTask: [],
            formBasis: {
                sort: '',
            },
            ruleBasis: {
                sort: [validateRules.required('基础任务名称')[0]]
            },
            // 勾选的数据
            checkTask: [],
            selectionList: [],
            // 基础任务排序
            tableDataBasicTaskSort: [],
            tableColumnsBasicTaskSort,
            // 附加任务
            tableColumnsAdditionalTasks,
            tableColumnsAdditionalTasksSort,
            tableDataAdditionalTasksSort: [],
            // 单据
            tableColumnsDocument,
            tableColumnsDocumentSort,
            tableDataDocumentSort: [],
            // 选择场景
            formScenes: {
                symbiosisScene: '',
                exclusiveScene: ''
            },
            // 附加任务
            tableDataAdditionalTasks: [],
            // 单据
            tableDataDocument: [],
            tableColumns,
            tableData: [],
            isAdd: true,
            // modal任务选中的值
            selectedData: [],
            // 添加共存场景
            sceneLoading: false,
            // list: ['北京', '重庆'],
            list: [{sceneCode: '111', sceneName: '场景1'},{sceneCode: '222', sceneName: '场景2'}],
            sceneData: [],
            sceneSelect: [],
            sceneSelectList: [],
            // 添加不必要共存场景
            sceneNoLoading: false,
            sceneNoData: [],
            sceneNoSelect: [],
            sceneNoSelectList: [],
            // 审核信息
            examineInfo: {},
             // 审核同意、不同意
            agree: '1',
            // 不同意驳回原因
            reason: '',
            // 测试
            test: [],
            serviceTag: '服务类型',
            transportTag: '运输类型',
            logisticsTag: '物流类型',
            dataList: []
        }
    },
    mounted() {
        this.getDictionaryData()
        // 获取存储在本地sessionStorage的账户信息
        this.userType = JSON.parse(window.sessionStorage.getItem('logisticsData'))
        this.userType = "管理员"
        // this.userType = "用户"
        if (this.userType == "管理员") {
            this.isManager = true
        }
    },
    methods: {
        addvisibleChange (changeStatus) {
            this.isModalShow = changeStatus
        },
        auditvisibleChange (changeStatus) {
            this.isAuditShow = changeStatus
        },
        visibleChange (changeStatus) {
            this.isActionDel = changeStatus
        },
        selectionChange (selectionList) {
            this.selectionList = selectionList
            // console.log(this.selectionList)
        },
        // 获取字典数据
        getDictionaryData () {
            // 服务类型
            this.$getDictionary('SERVICE_TYPE')
            // 运输类型
            this.$getDictionary('TRANSPORT_MODE')
        },
        dataInit () {
            this.getData()
        },
        getData () {
            const data = {
                "direction": 'asc', //"排序方式（asc 正序  desc 倒序）",
                "examineStatus":  this.search.examineStatus,// "审核状态",
                // "logisticsType": ,//"物流状态",
                "page": this.page, //当前页码,
                // "properties": "排序字段",
                "sceneCode": this.search.sceneCode, //"场景编码",
                "sceneName": this.search.sceneName, //"场景名称（模糊匹配）",
                "serverType": this.search.serverType,//"服务类型",
                // "size": 2,
                "trspType": this.search.trspType,
                "useStatus": this.search.useStatus //"启用状态"
            }
            apiQueryPage({ data: data }).then(res => {
                if (res.code == 200) {
                    this.tableData = res.data.content || []
                    this.total = res.data.totalElements
                    this.tableData.forEach(element => {
                        element.createDatetime = dateToString(element.createDatetime)
                        element.examineDatetime = dateToString(element.examineDatetime)
                    })
                }
            })
        },
        add () {
            this.isModalShow = true
            this.distinguish = 1
            this.modelTitle = '新增场景'
            this.isAdd = true
            this.getAddData()
        },
        getAddData (val1, val2, val3) {
            if (val1 || val2 || val3) {
                // 服务类型为运输/场所
                const data = {
                    serverType: val1 || '',
                    trspType: val2 || '',
                    logisticsType: val3 || ''
                }
                if (val1 == 'T' || val1 == 'Y') {
                    if (val2 && val3) {
                        this.addType = data
                        this.getTypeData(data)
                    }
                } else {
                    this.addType = data
                    this.getTypeData(data)
                }
            } else {
                const data = {
                    "serverType": "服务类型",
                    "trspType": "运输类型",
                    "logisticsType": "物流类型"
                }
                this.getTypeData(data)
            }
        },
        getTypeData (data) {
            apiInitSceneAdd({data: data}).then(res => {
                if (res.code == 200) {
                    this.tableDataBasicTask = res.data.baseTasks || []
                    this.tableDataAdditionalTasks = res.data.affixTasks || []
                    this.tableDataDocument = res.data.bills || []
                    this.sceneNoData = res.data.sceneInfo || []
                    this.sceneNoData.forEach(item => {
                        this.sceneData.push({
                            value: item.sceneName,
                            label: item.sceneCode
                        })
                    })
                    // console.log(this.sceneData)
                }
            })
        },
        startUsing (dataSource) {
            const data = this.handleRequestData(dataSource)
            // 批量操作，没有选择场景
            if (!data.length) return
            apiEnable({ data }).then(res => {
                this.$Message.info(res.message)
                this.getData()
            })
        },
        stopUsing (dataSource) {
            const data = this.handleRequestData(dataSource)
            // 批量操作，没有选择场景
            if (!data.length) return
            apiDisable({ data }).then(res => {
                this.$Message.info(res.message)
                this.getData()
            })
        },
        // 将数据处理成ajax请求需要的入参数据
        handleRequestData (data) {
            const requestData = []
            // 单个启用、停用
            if (data) {
                requestData.push(handle(data))
                return requestData
            } else {
                // 批量启用、停用
                if (!this.selectionList.length) {
                    this.$Message.warning('请先选择场景')
                } else {
                    for (const list of this.selectionList) {
                        requestData.push(handle(list))
                    }
                }
                return requestData
            }
            function handle (obj) {
                const { sceneCode, taskCode, useStatus, version, examineStatus } = obj
                return {
                    sceneCode,
                    taskCode,
                    useStatus,
                    version,
                    examineStatus
                }
            }
        },
        // 下拉框选择触发
        selectType (val) {
            if (val.tag == '服务类型') {
                this.serverValue = val.value
            }
            if (val.tag == '运输类型') {
                this.trspValue = val.value
            }
            if (val.tag == '物流类型') {
                this.logisticsValue = val.value
            }
            this.getAddData(this.serverValue, this.trspValue, this.logisticsValue)
        },
        // 勾选新增基础任务
        checkBasicTask (row) {
            this.selectedData[this.btnIndex] = row
        },
        // 排序
        sort (index) {
            // console.log(index)
            if (index == 1) {
                if (!this.verifyItem()) return
                this.tableDataBasicTaskSort = this.selectedData[this.btnIndex]
            }
            if (index == 2) {
                if (!this.verifyItem()) return
                this.tableDataAdditionalTasksSort = this.selectedData[this.btnIndex]
            }
            if (index == 3) {
                if (!this.verifyItem()) return
                this.tableDataDocumentSort = this.selectedData[this.btnIndex]
            }
        },
        // 新增搜索
        searchTask (index) {
            const data = this.addType
            if (index == 1) {
                data.taskName = this.basicTaskName
                console.log(data)
                apiQueryTaskBase({data: data}).then(res => {
                    if (res.code == 200) {
                        this.tableDataBasicTask = res.data || []
                    }
                })
            }
            if (index == 2) {
                data.taskName = this.affixTaskName
                apiQueryTaskAffix({data: data}).then(res => {
                    if (res.code == 200) {
                        this.tableDataAdditionalTasks = res.data || []
                    }
                })
            }
        },
        // sceneRemote (val) {
            // if (val !== '') {
            //     this.sceneLoading = true
            //     setTimeout(() => {
            //         this.sceneLoading = false
            //         const list = this.list.map(item => {
            //             return {
            //                 value: item,
            //                 label: item
            //             }
            //         })
            //         this.sceneData = list.filter(item => item.label.toLowerCase().indexOf(val.toLowerCase()) > -1)
            //     }, 200)
            // } else {
            //     this.sceneData = []
            // }
        // },
        delTransit (index) {
            // this.sceneSelect.splice(index, 1)
            this.sceneSelectList.splice(index, 1)
        },
        onChangeClick(val) {
            this.sceneSelectList.push(val)
            this.sceneSelect.push(val.value)
        },
        // sceneNoRemote (val) {
        //     if (val !== '') {
        //         this.sceneNoLoading = true
        //         setTimeout(() => {
        //             this.sceneNoLoading = false
        //             const list = this.list.map(item => {
        //                 return {
        //                     value: item,
        //                     label: item
        //                 }
        //             })
        //             this.sceneNoData = list.filter(item => item.label.toLowerCase().indexOf(val.toLowerCase()) > -1)
        //         }, 200)
        //     } else {
        //         this.sceneNoData = []
        //     }
        // },
        delTransitNo (index) {
            // this.sceneNoSelect.splice(index, 1)
            this.sceneNoSelectList.splice(index, 1)
        },
        noChangeClick(val) {
            this.sceneNoSelect.push(val.value)
            this.sceneNoSelectList.push(val)
            // // 数组去重
            // this.dataList.push(val)
            // let arr = new Set(this.dataList)
            // this.sceneNoSelectList = Array.from(arr)
        },
        // 任务发生改变
        changeRowData (row, index, key, judge) {
            // 将改行改变后的数据更新到accountData，后续选中才有数据
            // this.tableDataBasicTask[index] = row
            if (judge == 'sortBasic') {
                this.tableDataBasicTask[index] = row
            }
            if (judge == 'sortAttach') {
                this.tableDataAdditionalTasks[index] = row
            }
            if (judge == 'sortDocument') {
                this.tableDataDocument[index] = row
            }
            // this.tableDataBasicTask[index] = row
            // 先选中，再选择帐户类型，需要手动改变选中的帐户的帐户类型
            for (const item of this.selectedData[this.btnIndex]) {
                if (item.taskName === row.taskName) {
                    // 排序值在后面的排序要用到，要转换成Number类型，不然字符串之间比较大小会出错
                    // if (key === 'sortBasic' || key === 'sortAttach' || key === 'sortDocument') {
                    if (key === 'sort' ) {
                        // Number(row[key])可能会被转换成NaN
                        item[key] = Number(row[key])
                    } else {
                        item[key] = row[key]
                    }
                    break
                }
            }
        },
        searchChange () {},
        examine (row) {
            // 根据状态判断是启用审核
            if (row.useStatus !== 'enable') {
                this.examineType = 1
                this.examineModalTitle = '启用审核'
            } else {
                this.examineType = 0
                this.examineModalTitle = '停用审核'
            }
            // console.log(row.sceneCode)
            this.examineInfo.sceneCode = row.sceneCode
            this.examineInfo.version = row.version
            this.isAuditShow = true
        },
        // 审核
        audit (row) {
            this.isAuditShow = true
        },
        // 编辑
        edit (row) {
            this.isModalShow = true
            this.distinguish = 2
            this.modelTitle = '编辑场景'
            this.isAdd = false
            this.sceneCode = row.sceneCode
            this.editFormItem = row
            const data = {
                serverType: row.serverType,
                trspType: row.trspType,
                logisticsType: row.logisticsType
            }
            this.getTypeData(data)
        },
        // 删除
        remove () {
            this.isActionDel = true
        },
        allDelClick () {},
        // 新增场景保存
        saveAdd () {
            const vm = this
            this.$refs.addFormItemData.validate((valid) => {
                if (valid) {
                    if (!this.verifyItem()) return
                    const data = { ...this.addFormItem }
                    // data.baseTasks = this.tableDataBasicTaskSort
                    // data.affixTasks = this.tableDataAdditionalTasksSort
                    // data.blsCodes = this.tableDataDocumentSort
                    data.baseTasks = this.selectedData[1]
                    data.affixTasks = this.selectedData[2]
                    data.blsCodes = this.selectedData[3]
                    data.symbiosisScenes = this.sceneSelect
                    data.mutexScenes = this.sceneNoSelect
                    // 新增保存
                    if (this.isAdd) {
                        apiAdd({ data: data }).then(res => {
                            successCallback()
                        })
                    } else {
                        // 编辑保存
                        apiAdd({ data: data }).then(res => {
                            successCallback()
                        })
                    }
                }
            })
            function successCallback () {
                vm.$Message.info("保存成功")
                // 重置表单数据
                vm.$refs.addFormItemData.resetFields()
                // 重置选中的任务
                vm.selectedData = []
                // 隐藏新增模态框
                vm.isModalShow = false
                // 重置场景选择框
                vm.sceneNoData = []
                // 刷新列表
                vm.getData()
            }
        },
        // 新增任务保存、排序校验
        verifyItem () {
            // console.log(this.selectedData)
            // 至少选择一个任务
            if (!this.selectedData[this.btnIndex].length) {
                this.$Message.warning('请至少选择一个任务！')
                return false
            }
            const sort = []
            for (const item of this.selectedData[this.btnIndex]) {
                // 勾选的任务的排序值必填
                // 排序值可能会被转换成NaN
                if (!item.sort && !Number.isNaN(item.sort)) {
                    this.$Message.warning('选中任务的排序值必填！')
                    return false
                }
                // 排序值只能是正整数
                if (!/^\d+$/.test(item.sort)) {
                    this.$Message.warning('排序值只能是整数！')
                    return
                }
                sort.push(item.sort)
            }
            // 勾选的任务的排序值不能重复
            if (isRepeat(sort)) {
                this.$Message.warning('选中的任务的排序值不能重复！')
                return false
            }
            return true
        },
        // 同意审核
        saveAudit () {
            const data = { ...this.examineInfo}
            if (this.examineType === 1) {
                // 同意
                if (this.agree === '1') {
                    apiExamEnablePass({ data }).then(res => {
                        this.$Message.info(res.message)
                        this.isAuditShow = false
                        this.getData()
                    })
                } else {
                    if (!this.reason) {
                        this.$Message.warning('请填写输入驳回原因！')
                        return
                    }
                    data.message = this.reason
                    // 不同意
                    apiExamEnableFail({ data }).then(res => {
                        this.$Message.info(res.message)
                        this.isAuditShow = false
                        this.getData()
                    })
                }
            } else {
                // 停用审核
                // 同意
                if (this.agree === '1') {
                    apiExamDisablePass({ data }).then(res => {
                        this.$Message.info(res.message)
                        this.isAuditShow = false
                        this.getData()
                    })
                } else {
                    if (!this.reason) {
                        this.$Message.warning('请填写输入驳回原因！')
                        return
                    }
                    data.message = this.reason
                    // 不同意
                    apiExamDisableFail({ data }).then(res => {
                        this.$Message.info(res.message)
                        this.isAuditShow = false
                        this.getData()
                    })
                }
            }
        },
        // 新增取消
        cancelAdd () {
            // 重置场景选择框
            this.sceneNoData = []
        },
        detail (row) {
            const data = row.sceneCode
            this.$router.push({path: '/standard-mapper/scene-manage/detail', query: {sceneCode: data}})
        },
        auditStatus (id) {
            if (this.search.examineStatus === id) {
                this.search.examineStatus = ''
            } else {
                this.search.examineStatus = id
            }
        },
        enableStatus (id) {
            // 控制状态切换传值
            if (this.search.useStatus === id) {
                this.search.useStatus = ''
            } else {
                this.search.useStatus = id
            }
        },
        // 切换tab
        handleBtn (index) {
            this.btnIndex = index + 1
            // tabs切换的时候，将勾选的数据置空避免带到下一个tabs列表下
            // this.selectedData = []
        }
    },
}
</script>

